.Home {
    width: 100vw;
    height: 100vh;
    margin: 0;
    background-color: white;
    display: flex;
    overflow: auto;
}

.home-zone {
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

.home-pic-1 {
    width: 100vw;
    height: 100vh;
    background: url("../pictures/toxic_till_the_end.jpeg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

#home-pic-1-gradient {
    width: 100vw;
    height:30%;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.76), transparent);
}
