.Header-menu {
    max-width: 100%;
    height: 50px;
    display: flex;
    padding: 18px 35px 0 35px;
    align-items: center;
    justify-content: space-between;
    background: transparent;
}

#logo {
    width: 100px;
    height: 50px;
    background: url("../pictures/logo.png");
    background-size: contain;
    background-repeat: no-repeat;
    border: 0;
}

#logo:hover {
    cursor: pointer;
}

#navbar {
    width: 450px;
    height: 50px;
    margin: auto 0;
}

@media (max-width: 768px) {
    .Header-menu {
        max-width: 100%;
        height: 50px;
        display: flex;
        padding: 18px 35px;
        background-color: transparent;
    }
    
    #logo {
        width: 100px;
        height: 50px;
    }
}
  
@media (min-width: 769px) and (max-width: 1024px) {
    .Header-menu {
        max-width: 100%;
        height: 50px;
        display: flex;
        padding: 18px 35px;
        background-color: transparent;
    }
    
    #logo {
        width: 100px;
        height: 50px;
    }
}
  