.Navbar {
    width: 100%;
    height: 60%;
    background-color: transparent;
    display: flex;
    padding: 10px 0;
    justify-content: space-between;
}

.nav-buttton {
    background-color: transparent;
    border: 0;
    font-size: 15px;
    color: #ffd3e5;
    min-width: 150px;
}

.nav-buttton:hover {
    color: #ffffff;
    cursor: pointer;
    font-weight: bold;
}